$icon-padding: 1.2rem;

.link-field {
  background-color: white;
  border-color: $input-border-color;
  border-width: 1px;
  border-style: solid;
  border-radius: 3px;
  height: 54px;
  width: 100%;
  color: $link-color;
  display: flex;
  align-content: center;
  flex-wrap: wrap;
  padding: .4rem $icon-padding;

  &__icon {
    display: block;
    margin-right: $icon-padding;
    font-size: 1.4rem;
    height: 1.6rem;
  }

  &__content {

  }
}



