.link-field {
  background-color: white;
  border-color: #cbd3df;
  border-width: 1px;
  border-style: solid;
  border-radius: 3px;
  height: 54px;
  width: 100%;
  color: #0071c4;
  display: flex;
  align-content: center;
  flex-wrap: wrap;
  padding: 0.4rem 1.2rem;
}

.link-field__icon {
  display: block;
  margin-right: 1.2rem;
  font-size: 1.4rem;
  height: 1.6rem;
}




